<template>
  <div :id="POPUP_ID" class="popup" :style="style">
    <h3 class="title mb-4">
      {{
        multipleSelectionMode
          ? $t('message.select_groups')
          : $t('message.select_group')
      }}
      <a class="close" @click="resetAndClose">&times;</a>
    </h3>
    <div class="error-box mb-2" v-if="error !== null">
      {{ error.message }}
    </div>
    <vue-select
      :value="selectedGroupsOrGroup"
      :options="sortedGroups"
      @input="onSelectInput"
      :multiple="multipleSelectionMode"
      label="NAME"
    ></vue-select>
    <!--div>
      Model: {{ selectedGroupsOrGroup }}
    </div-->
    <button
      type="button"
      class="apply-btn"
      @click="applyGroupSelection"
    >
      {{ $t('message.choose') }}
    </button>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex';
import { VueSelect } from 'vue-select';

import { POPUP_ID } from './const';

export default {
  components: {
    VueSelect,
  },
  created() {
    this.getGroups();
  },
  computed: {
    POPUP_ID: () => POPUP_ID,
    ...mapState([
      'multipleSelectionMode',
      'style',
      'error',
    ]),
    ...mapGetters([
      'sortedGroups',
      'selectedGroups',
      'selectedGroupsOrGroup',
    ]),
  },
  methods: {
    onSelectInput(model) {
      this.setSelection({ model });
    },
    ...mapMutations([
      'setSelection',
    ]),
    ...mapActions([
      'getGroups',
      'applyGroupSelection',
      'resetAndClose',
    ]),
  },
};
</script>

