import Vue from 'vue';
import Vuex from 'vuex';
import VueI18n from 'vue-i18n';

import { POPUP_ID } from './const';
import { store } from './store';
import { messages } from './i18n';
import GroupSelector from './group-selector.vue';

Vue.use(Vuex);
try { Vue.use(VueI18n); } catch (e) { console.warn(e); }

export let t = message => {
  console.warn('App not initialized');
  return message;
};

let _onClose = () => {};

const createApp = ({
  callback,
  el = null,
  multiple = false,
  allowEmpty = false,
  locale = 'ru',
  initiallySelected,
  filter = (() => {}),
  style = {},
}) => {
  if (el === null) {
    el = document.createElement('div');
    el.id = POPUP_ID;
    document.body.appendChild(el);
  }
  const i18n = new VueI18n({
    locale: locale.toLowerCase(),
    messages,
  });
  t = i18n.t.bind(i18n);
  return new Vue({
    el: `#${ POPUP_ID }`,
    store: store(callback, multiple, allowEmpty, initiallySelected, filter, style),
    i18n,
    components: {
      GroupSelector,
    },
    template: '<group-selector></group-selector>',
  });
};

export const selectSonetGroup = ({
  callback = () => null,
  multiple = false,
  allowEmpty = false,
  initiallySelected = null,
  filter = (() => {}),
  onClose = (() => {}),
  locale = 'ru',
  style = {},
}) => {
  _onClose = onClose;
  if (!initiallySelected) {
    initiallySelected = [];
  } else if (
    typeof initiallySelected === 'string'
    || typeof initiallySelected.length === 'undefined'
  ) {
    initiallySelected = [String(initiallySelected)];
  } else {
    initiallySelected = initiallySelected.map(String);
  }
  createApp({
    callback, multiple,
    allowEmpty, initiallySelected, filter, locale, style,
  });
};

export const closeSonetGroupSelection = () => {
  const el = document.getElementById(POPUP_ID);
  if (el !== null) {
    el.parentElement.removeChild(el);
    _onClose();
  }
};

export default {
  selectSonetGroup,
  closeSonetGroupSelection,
};
