import Vue from 'vue';
import { Store } from 'vuex';

import { closeSonetGroupSelection, t } from './lib';

const state = (multipleSelectionMode, allowEmpty, style) => ({
  groups: {},
  selectedGroupIds: [],
  multipleSelectionMode,
  allowEmpty,
  style: { ...style },
  error: null,
});
const getters = (filter) => ({
  sortedGroups: state => {
    return Object.values(state.groups)
      .filter(filter)
      .sort((g1, g2) =>
        g1.NAME.toLowerCase() < g2.NAME.toLowerCase()
          ? -1
          : Number(g1.NAME.toLowerCase() > g2.NAME.toLowerCase()));
  },
  selectedGroups: state =>
    state.selectedGroupIds.map(id => state.groups[id]),
  selectedGroupsOrGroup: state =>
    selectedGroupsOrGroup(state),
});

const selectedGroupsOrGroup =
  ({ groups, selectedGroupIds, multipleSelectionMode }) =>
    multipleSelectionMode
      ? [ ...selectedGroupIds.map(id => groups[id]) ]
      : selectedGroupIds.length === 1
        ? { ...groups[selectedGroupIds[0]] }
        : null;

const isEmptySelection = ({ selectedGroupIds }) =>
  selectedGroupIds.length === 0;

const arrEq = (a1, a2) => {
  if (a1.length !== a2.length) { return false; }
  return a1.every((item, ix) => item === a2[ix]);
};

const mutations = {
  setSelection: (state, { model }) => {
    // model -    1 
    const selection = state.selectedGroupIds;
    if (state.multipleSelectionMode) {
      const newSelection = model.map(u => u.ID);
      // condition to prevent watchers from endless cycle
      if (arrEq(selection, newSelection)) { return; }
      state.selectedGroupIds = newSelection;
    } else {
      if (model === null) {
        // condition to prevent watchers from endless cycle
        if (selection.length !== 0) {
          state.selectedGroupIds = [];
        }
      } else {
        const ID = model.ID;
        // condition to prevent watchers from endless cycle
        if (selection.length === 1 && selection[0] === ID) { return; }
        state.selectedGroupIds = [ ID ];
      }
    }
    state.error = null;
  },
  setModelFromArray(state, { groupIds }) {
    this.commit('setSelection', {
      model: state.multipleSelectionMode
        ? groupIds
          .filter(uid => state.groups.hasOwnProperty(uid))
          .map(uid => ({ID: uid}))
        : groupIds.length !== 0
          ? {ID: groupIds[0]}
          : null,
    });
  },
  addGroups: (state, groups) => {
    Vue.set(state, 'groups', groups.reduce((map, group) => {
      map[group.ID] = group;
      return map;
    }, { ...state.groups }));
  },
  onError(state, { message, error = null }) {
    state.error = { message, error };
  },
};

const actions = (selectGroupCallback, initiallySelected) => ({
  getGroups: ({ commit }) => {
    const onSuccess = groups => {
      commit('addGroups', groups);
      if (initiallySelected.length) {
        commit('setModelFromArray', { groupIds: initiallySelected });
      }
    };
    const onError = error => {
      commit('onError', {
        error,
        message: t('message.error_fetching_groups_list'),
      });
    };
    const groups = [];
    window.BX24.callMethod('sonet_group.get', {}, result => {
      let error;
      // eslint-disable-next-line no-cond-assign
      if (error = result.error()) {
        onError(error);
      } else {
        groups.push(...result.data());
        if (result.more()) {
          result.next();
        } else {
          onSuccess(groups);
        }
      }
    });
  },
  applyGroupSelection: ({ state, commit }) => {
    if (!state.allowEmpty && isEmptySelection(state)) {
      commit('onError', { message: t('message.select_group') });
      return;
    }
    closeSonetGroupSelection();
    selectGroupCallback(selectedGroupsOrGroup(state));
  },
  resetAndClose: ({ state, commit }) => {
    commit('setSelection', {
      model: state.multipleSelectionMode ? [] : null,
    });
    closeSonetGroupSelection();
  },
});


export const store = (
  selectGroupCallback, multiple,
  allowEmpty, initiallySelected, filter, style,
) => new Store({
  state: state(multiple, allowEmpty, style),
  getters: getters(filter),
  mutations,
  actions: actions(selectGroupCallback, initiallySelected),
});
